@mixin center-margin($width: 1300px) {
    max-width: $width;
    margin: 0 auto;
}

@mixin heading ($fontsize) {
    text-transform: uppercase;
    font-weight: 500;
    line-height: normal;
    font-size: $fontsize;
}

body {
    font-size: var(--font-size);
    font-weight: normal;
    color: var(--gray);
}


h1 {
    @include heading(12.5rem);
    font-family: "Guilty Pleasure";
    color: var(--green-transparent);
    text-align: center;
}

h2,
#header h1 {
    @include center-margin();
    @include heading(2.25rem);
    color: var(--green);
    margin-bottom: var(--inner-section-padding);
}

h3 {
    @include heading(1.5rem);
}

section {
    max-width: 1250px;
    margin: 0 auto;
    padding: 0 1rem;
}

p {
    @include center-margin(900px);
    line-height: 170%;
}

.custom-cursor{
  line-height: 1;
  position: fixed;
  z-index: 999; 
  pointer-events: none;
  & .cursor-bubble{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1.5rem;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);

  }
  .cursor-box{
    color: var(--brown);
    position: absolute;
    left: 0.5em;
    right: 0.5em;
    top: 0.5em;
    bottom: 0.5em;
    transition: all 0.3s var(--ease);
    & .corner{
      width: 0.5em;
      height: 0.5em;
      &::after{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 1px;
        height: 100%;
        background: var(--brown);
      }
      &::before{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background: var(--brown);
      }
      &.left-top{
        top: 0;
        left: 0;
        position: absolute;
        
      }
      &.right-top{
        top: 0;
        right: 0;
        position: absolute;
        transform: rotate(90deg);
      }
      &.left-bottom{
        bottom: 0;
        left: 0;
        position: absolute;
        transform: rotate(270deg);
      }
      &.right-bottom{
        bottom: 0;
        right: 0;
        position: absolute;
        transform: rotate(180deg);
      }

    }
  }
}
