/* http://meyerweb.com/eric/tools/css/reset/ 
 *    v2.0 | 20110126
 *       License: none (public domain)
 *       */
* {
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --gray: #434343;
  --white: #F5F4F3;
  --green: #54906D;
  --green-transparent: rgba(84, 144, 109, 0.5);
  --dark-green: #2C3634;
  --brown: #716963;
  --black: #000000;
  --inner-section-padding: 4.5rem;
  --inner-component-padding: 2.75rem;
  --small-padding: 1.25rem;
  --large-gap: 3.75rem;
  --font-size: 1.25rem;
  --ease: cubic-bezier(.14,.8,.4,1);
  font-family: "Inter";
}

@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: medium;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: bold;
}
@font-face {
  font-family: "Guilty Pleasure";
  src: url("../fonts/Guilty_Pleasure_DEMO.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}
.hidden {
  display: none;
  visibility: hidden;
}

.show {
  display: block;
  visibility: visible;
}

.has-text-align-center {
  text-align: center;
}

.has-text-align-left {
  text-align: left;
}

.has-text-align-right {
  text-align: right;
}

.wp-block-buttons {
  width: fit-content;
  margin: var(--inner-component-padding) auto;
}
.wp-block-buttons .wp-block-button {
  position: relative;
  border: 2px solid var(--gray);
  border-radius: none;
  background-color: transparent;
  cursor: pointer;
}
.wp-block-buttons .wp-block-button .wp-block-button__link {
  position: relative;
  padding: 1.5rem 2.75rem;
  color: var(--gray);
  background-color: transparent;
  font-size: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  transition: all 0.3s var(--ease);
  z-index: 2;
}
.wp-block-buttons .wp-block-button::before {
  content: "";
  position: absolute;
  bottom: -10px;
  left: -5px;
  width: 0%;
  height: 100%;
  transform-origin: center left;
  rotate: -4deg;
  background-color: var(--green-transparent);
  transition: all 0.3s var(--ease);
  z-index: 1;
}
.wp-block-buttons .wp-block-button:hover::before, .wp-block-buttons .wp-block-button:focus::before {
  width: calc(100% + 10px);
}

.wp-block-heading, wp-block-image {
  user-select: none;
}

body {
  font-size: var(--font-size);
  font-weight: normal;
  color: var(--gray);
}

h1 {
  text-transform: uppercase;
  font-weight: 500;
  line-height: normal;
  font-size: 12.5rem;
  font-family: "Guilty Pleasure";
  color: var(--green-transparent);
  text-align: center;
}

h2,
#header h1 {
  max-width: 1300px;
  margin: 0 auto;
  text-transform: uppercase;
  font-weight: 500;
  line-height: normal;
  font-size: 2.25rem;
  color: var(--green);
  margin-bottom: var(--inner-section-padding);
}

h3 {
  text-transform: uppercase;
  font-weight: 500;
  line-height: normal;
  font-size: 1.5rem;
}

section {
  max-width: 1250px;
  margin: 0 auto;
  padding: 0 1rem;
}

p {
  max-width: 900px;
  margin: 0 auto;
  line-height: 170%;
}

.custom-cursor {
  line-height: 1;
  position: fixed;
  z-index: 999;
  pointer-events: none;
}
.custom-cursor .cursor-bubble {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.custom-cursor .cursor-box {
  color: var(--brown);
  position: absolute;
  left: 0.5em;
  right: 0.5em;
  top: 0.5em;
  bottom: 0.5em;
  transition: all 0.3s var(--ease);
}
.custom-cursor .cursor-box .corner {
  width: 0.5em;
  height: 0.5em;
}
.custom-cursor .cursor-box .corner::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 100%;
  background: var(--brown);
}
.custom-cursor .cursor-box .corner::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: var(--brown);
}
.custom-cursor .cursor-box .corner.left-top {
  top: 0;
  left: 0;
  position: absolute;
}
.custom-cursor .cursor-box .corner.right-top {
  top: 0;
  right: 0;
  position: absolute;
  transform: rotate(90deg);
}
.custom-cursor .cursor-box .corner.left-bottom {
  bottom: 0;
  left: 0;
  position: absolute;
  transform: rotate(270deg);
}
.custom-cursor .cursor-box .corner.right-bottom {
  bottom: 0;
  right: 0;
  position: absolute;
  transform: rotate(180deg);
}

.headerParallax {
  position: relative;
  height: 150vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.headerParallax .hero-transition-gradient {
  height: 60vh;
  width: 100%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgb(255, 255, 255) 35%, rgb(255, 255, 255) 75%, rgba(255, 255, 255, 0) 100%);
  position: absolute;
  top: 120vh;
  left: 0;
  z-index: 100;
}
.headerParallax .hero-text {
  margin-top: 20vh;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 7rem;
  font-weight: 700;
  text-transform: uppercase;
  position: relative;
  text-shadow: 0px 0px 100px rgb(0, 0, 0);
}
.headerParallax .parallax-layer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  object-fit: fill;
}

nav {
  position: relative;
  z-index: 101;
  background-color: var(--brown);
  border-radius: 3rem;
  color: var(--white);
  padding: 1.5rem;
  width: 80%;
  left: 10%;
  margin-bottom: var(--inner-section-padding);
}
nav ul {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 2rem;
}
nav ul li:first-child {
  margin-right: auto;
}

.icon-grid {
  display: grid !important;
  margin: var(--inner-section-padding) auto;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, 300px);
  gap: 100px;
  text-align: center;
}
.icon-grid p {
  text-align: center;
}
.icon-grid h3 {
  color: var(--green);
  margin-bottom: var(--small-padding);
}

main.adventure {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 1rem;
  display: grid;
  grid-template-columns: 3fr 4fr;
  gap: var(--large-gap);
}
main.adventure .adventure-image {
  background-color: gray;
  width: 100%;
  aspect-ratio: 9/21;
}
main.adventure h2 {
  color: var(--gray);
}
main.adventure p:not(.location) {
  margin-bottom: var(--small-padding);
}

@media screen and (max-width: 900px) {
  main.adventure {
    grid-template-columns: 1fr;
    gap: var(--small-gap);
  }
  main.adventure .adventure-image {
    aspect-ratio: 9/16;
    margin-bottom: var(--small-padding);
  }
}